@import "../../../../../Variables.scss";

.signUpdiv {
  width: 100%;
  height: 80vh;
  margin: 30px auto;
  background: url("../../../../../assets/imgs/bg.png") no-repeat center/cover;
  h2 {
    text-align: center;
    margin-bottom: 30px;
  }
  .signUpDiv {
    width: 30%;
    margin: 20px auto;
    .signUpInputs {
      width: 100%;
    }
    .errorLabel {
      color: red;
      font-weight: lighter;
    }
  }
  .Already {
    text-align: center;
    margin-top: 20px;
  }
  Button {
    width: 30%;
    display: block;
    margin: auto;
    background-color: $primaryColor;
  }
  @media screen and (max-width: 992px) {
    .signUpDiv {
      width: 50%;
    }
  }
  @media screen and (max-width: 576px) {
    .signUpDiv {
      width: 80%;
    }
  }
}
